.certificate-wrapper {
  width: 30%;
  height: 10%;
  opacity: 1;
  transition: all 0.3s ease-in-out;

  .certificate {
    border: 6px solid #702626;
    transition: all 0.3s ease-in-out;
    width: 100%;
  }

  &:hover {
    transition: all 0.3s ease-in-out;
    box-shadow: 0px -4px 76px #111111;
    width: 30.5%;
  }
}
